.check-collection {
  display: block;
  position: relative;
}

.check-collection:hover .check-collection__bg {
  background-color: #eeeeee;
}

.check-collection input {
  position: absolute;
  left: 0;
  top: -1px;
  opacity: 0;
  width: 16px;
  height: 16px;
}

.check-collection input:checked ~ .check-collection__bg {
  background-color: #0078ff;
}

.check-collection input:checked ~ .check-collection__image .check-collection__check {
  visibility: visible;
  opacity: 1;
}

.check-collection input:checked ~ .check-collection__text {
  color: #fff;
}

.check-collection input:checked ~ .check-collection__private {
  background-color: #fff;
  color: #0078ff;
}

.check-collection__check {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 10px;
  right: 10px;
  height: 35px;
  width: 35px;
  border-radius: 50%;
  background-color: #fff;
  font-size: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.9s, visibiliti 0.9s;
}

.check-collection__check .icon {
  font-size: 15px;
  color: #0078ff;
}

.check-collection__bg {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
  border-radius: 5px;
  transition: background-color 0.9s;
}

.check-collection__private {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  background: #8e8e8e;
  border-radius: 50%;
  height: 20px;
  width: 20px;
  line-height: 20px;
  font-size: 0;
  color: #fff;
  text-align: center;
  transition: background-color 0.9s, color 0.9s;
}

.check-collection__private .icon {
  font-size: 9px;
  vertical-align: middle;
}

.check-collection__helper {
  font-size: 13px;
  color: #8e8e8e;
  margin-top: 16px;
  display: none;
}

.check-collection.is-repeated input:checked ~ .check-collection__bg {
  bottom: 32px;
}

.check-collection.is-repeated input:checked ~ .check-collection__helper {
  display: block;
}

.check-collection.is-repeated input:checked ~ .check-collection__private {
  top: calc(50% - 17px);
}

