.toggler {
  position: relative;
  padding-left: 96px;
  cursor: pointer;
  display: block;
  user-select: none;
}

.toggler input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.toggler input:checked ~ .toggler__text:before {
  background-color: #0078ff;
}

.toggler input:checked ~ .toggler__text:after {
  transform: translateX(40px);
}

.toggler__text {
  display: inline-block;
  vertical-align: middle;
  line-height: 40px;
  font-size: 15px;
}

.toggler__text:before, .toggler__text:after {
  position: absolute;
  content: '';
}

.toggler__text:before {
  left: 0;
  top: 0;
  width: 80px;
  height: 40px;
  background-color: #e2e2e2;
  outline: none;
  border-radius: 20px;
  transition: background-color .3s;
}

.toggler__text:after {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  top: 3px;
  left: 3px;
  background-color: #fff;
  transition: transform .3s;
}
