.product {
	position: relative;
	input {
		position: absolute;
		left: 0;
		top: -1px;
		opacity: 0;
		width: 16px;
		height: 16px;
		&:checked ~ .product__check {
			opacity: 1; } }
	&__image {
		display: block;
		padding-bottom: 100%;
		border-radius: 8px;
		@include cover; }
	&__check {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 10px;
		right: 10px;
		height: 35px;
		width: 35px;
		border-radius: 50%;
		background-color: #fff;
		font-size: 0;
		opacity: 0;
		transition: opacity .3s;
		.icon {
			font-size: 15px;
			color: $blue; } } }

