.out {
	position: relative;
	overflow: hidden; }

.inner {
	position: relative;
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1246px;
	@include clr; }

.row {
	margin: 0 -8px;
	display: flex;
	flex-wrap: wrap;
	box-sizing: border-box;
	&:before,
	&:after {
		box-sizing: border-box; } }

.col {
	padding: 0 8px;
	box-sizing: border-box;
	&:before,
	&:after {
		box-sizing: border-box; }
	&-25 {
		flex: 0 0 25%; }
	&-33 {
		flex: 0 0 33.3%;
		width: 33.3%; }
	&-42 {
		flex: 0 0 42%; }
	&-50 {
		flex: 0 0 50%; }
	&-58 {
		flex: 0 0 58%; }
	&-66 {
		flex: 0 0 66.7%;
		width: 66.7%; }
	&-75 {
		flex: 0 0 75%; }
	&-100 {
		flex: 0 0 100%; }



	&-md-50 {
		@include md {
			flex: 0 0 50%; } }
	&-md-100 {
		@include md {
			flex: 0 0 100%; } }

	&-sm-100 {
		@include sm {
			flex: 0 0 100%; } } }

.md-hide {
	@include md {
		display: none; } }
