.toggler {
    position: relative;
    padding-left: 96px;
    cursor: pointer;
    display: block;
    user-select: none;
    input {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        &:checked ~ .toggler__text {
            &:before {
                background-color: $blue; }
            &:after {
                transform: translateX(40px); } } }
    &__text {
        display: inline-block;
        vertical-align: middle;
        line-height: 40px;
        font-size: 15px;
        &:before,
        &:after {
            position: absolute;
            content: ''; }
        &:before {
            left: 0;
            top: 0;
            width: 80px;
            height: 40px;
            background-color: $extra-light-grey;
            outline: none;
            border-radius: 20px;
            transition: background-color .3s; }
        &:after {
            width: 34px;
            height: 34px;
            border-radius: 50%;
            top: 3px;
            left: 3px;
            background-color: $white;
            transition: transform .3s; } } }
